import React from 'react'
import { getAction,postAction } from '@/axios';
import {Modal,message} from 'antd';
import { withRouter } from 'react-router-dom';

class Details extends React.Component{
    constructor (props) {
        super(props);
        this.state = {
            record:{}
            // taskName:'-',
            // startTime:'2019-04-11 24:00',
            // endTime:'2019-04-11 24:00',
            // taskType:"1",//
            // taskId:"0091"//id
        }
    }
    componentDidMount(){
        this.getMsgDetail();
    }
    //
    getMsgDetail=()=>{
        getAction(`ybt/message/getMessage?id=${this.props.currentMsgId}`).then(res => {
            if(res.success){
                // console.log(res.obj);
                this.setState({
                    record:res.obj||{}
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    jumpTo = () => {
        //taskType
        // 21435
        if(this.state.record.templateType===3){//
            getAction(`/ybt/form/task/info`, {//
                formTaskId: this.state.record.businessId
            }).then(res=>{
                if(res.success){
                    if(res.obj && res.obj.taskStatus===5){
                        message.info(",");
                        return false;
                    }else{
                        this.props.history.push(`/myFillingTask/step2?id=${this.state.record.businessId}&type=fill`)
                    }
                }else{
                    message.error(res.obj)
                }
            })
        }else if(this.state.record.templateType===5){//
            console.log("")
        }else{//
            postAction("/ybt/wf/approveTask/getApproveNodeTaskById",{processNodeTaskId:this.state.record.businessId}).then(res=>{
                console.log(res);
                if(res.success){
                    if(!res.obj.taskEndTime){
                        this.props.history.push(`/auditManagement/audit?id=${this.state.record.businessId}`)
                    }else{
                        message.warning("")
                    }
                }else{
                    message.error(res.obj)
                }
            })
        }
    }
    render(){
        let {record} = this.state;
        let time=null
        if(record.messageTemplateId === '1' || record.messageTemplateId === '4' || record.messageTemplateId === '2'){
            time=<React.Fragment>
                <p><span></span><span>{record.taskPlanStartTime}</span></p>
                <p><span></span><span>{record.taskPlanEndTime}</span></p> 
            </React.Fragment>
        }else if(record.messageTemplateId === '3' || record.messageTemplateId === '9'){
            if(record.taskType === '1'){
                time=<React.Fragment>
                <p><span></span><span>{record.taskPlanStartTime}</span></p>
                <p><span></span><span>{record.taskPlanEndTime}</span></p> 
                </React.Fragment>
            }else{
                time=<React.Fragment>
                <p><span></span><span>{record.taskStartTime}</span></p>
                </React.Fragment>
            }
        }
        let link=null
        if(record.urlFlag === 1){
            // 
            if(record.templateType !== 5){
                //
                link=<span style={{color:"#4164E4", cursor:"pointer", textDecoration:'underline'}} onClick={this.jumpTo}></span>
            }
        }
        return (
            <Modal
                title=""
                visible={true}
                onCancel={this.props.hideDetails}
                footer = {null}
                bodyStyle={{padding: "28px 50px", lineHeight: "30px"}}
            >
                <p>{record.messageContent}</p>
                <p><span></span><span>{record.messageTitle}</span></p>
                {time}
                {link}
            </Modal>
        )
    }
}

export default withRouter(Details)